(function() {
  'use strict';

  angular
    .module('coherentPaDemo')
    .controller('TravelPurchase3Controller', ctrl);

  /** @ngInject */
  function ctrl($log,
                $state,
                DATE_FORMAT,
                OPTIONS_PAYLOAD,
                TRAVEL_TYPE,
                TRAVEL_PLANS_PAYLOAD,
                TRAVEL_FOLDER,
                PurchaseFactory,
                PurchaseService,
                TravelPurchaseService) {
    var vm = this;

    vm.ui = {};

    vm.proceed = function() {
      TravelPurchaseService.policy.defaultCoverage = vm.ui.defaultCoverage;
      TravelPurchaseService.policy.optionalBenefits = vm.ui.options;
      PurchaseService.policy.type = TRAVEL_TYPE;
      PurchaseService.policy.carrier = TRAVEL_FOLDER;
      PurchaseService.policy.specs = TravelPurchaseService.policy;
      PurchaseService.policy.specs.installmentPermium = vm.ui.planType.price;
      PurchaseService.policy.specs.planDetails = angular.copy(TravelPurchaseService.policy.planType);
      console.log(PurchaseService.policy.specs.planDetails)
      var a = TravelPurchaseService.policy.tripCount;
      var b = TravelPurchaseService.policy.planType.name;
      PurchaseService.policy.specs.planType = a;
      PurchaseService.policy.specs.planLevel = b;
      $state.go('purchase-1');
    };

    vm.getPrice = function () {
      PurchaseFactory.token().then( function(token) {
        PurchaseFactory.get(TRAVEL_TYPE, TRAVEL_FOLDER, generatePayload(), token).then(function (data) {
          vm.ui.planType.price = TravelPurchaseService.policy.planType.price = data.data.data.Outputs.BasePremium;
        });
      });
    };

    function generatePayload() {
      var payload = angular.copy(TRAVEL_PLANS_PAYLOAD);

      var DATE_FORMAT = 'YYYYDDD';

      payload.Inputs.Region = TravelPurchaseService.policy.whereAreYouGoing;
      payload.Inputs.Plan_Level = TravelPurchaseService.policy.planType.name;
      payload.Inputs.Adults = TravelPurchaseService.policy.totalAdults;
      payload.Inputs.Children = TravelPurchaseService.policy.additionalChildren;
      payload.Inputs.Insured_Person_Self = TravelPurchaseService.policy.myself ? 'Y' : 'N';
      payload.Inputs.Insured_Person_Spouse = TravelPurchaseService.policy.spouse ? 'Y' : 'N';
      payload.Inputs.Plan = TravelPurchaseService.policy.tripCount;
      payload.Inputs.Travel_Start_Date = parseInt(moment(TravelPurchaseService.policy.departureDate).format(DATE_FORMAT), 10);
      payload.Inputs.Travel_End_Date = parseInt(moment(TravelPurchaseService.policy.arrivalDate).format(DATE_FORMAT), 10);
      payload.TransactionDate = moment().format('YYYY-MM-DDThh:mm:ss.ssssss') + 'Z';

      payload.Inputs.Optional_Benefits = angular.toJson(_.map(vm.ui.options, function (option) {
        return {
          Option: option.name,
          Included: option.enabled ? 'y' : 'n',
          ClaimLimit: option.enabled ? option.value : 0
        }
      }));

      return payload;
    }

    function init() {
      TravelPurchaseService.debug();

      if(_.isEmpty(TravelPurchaseService.policy.planType)) {
        $log.debug('plan type is not defined. redirecting to step 1');
        $state.go('travel-purchase-1');
        return;
      }

      vm.ui.whereAreYouGoing = TravelPurchaseService.policy.whereAreYouGoing;
      vm.ui.departureDate = moment(TravelPurchaseService.policy.departureDate).format("MM/DD/YYYY");
      vm.ui.arrivalDate = moment(TravelPurchaseService.policy.arrivalDate).format("MM/DD/YYYY");
      vm.ui.totalAdults = TravelPurchaseService.policy.totalAdults;
      vm.ui.additionalChildren = TravelPurchaseService.policy.additionalChildren;
      vm.ui.planType = TravelPurchaseService.policy.planType;

      vm.ui.defaultCoverage = TravelPurchaseService.policy.defaultCoverage;
      vm.ui.minDefaultCoverage = 0;
      vm.ui.maxDefaultCoverage = 200;
      vm.ui.options = TravelPurchaseService.optionalBenefits;
      if (!TravelPurchaseService.optionalBenefits) {
        vm.ui.options = _.map(TravelPurchaseService.policy.options.benefits, function (option) {
          return {
            name: option.Option,
            label: option.Desc,
            isCheckbox: !!option.Column1,
            type: option.Column2,
            max: option.Column5,
            min: option.Column4,
            value: option.Column3,
            enabled: false
          };
        });
      }
    }

    init();
  }
})();

(function() {
  'use strict';

  angular
    .module('coherentPaDemo')
    .controller('TravelPurchase2Controller', ctrl);

  /** @ngInject */
  function ctrl($log,
                $state,
                DATE_FORMAT,
                TRAVEL_TYPE,
                TRAVEL_PLANS_PAYLOAD,
                TRAVEL_FOLDER,
                PurchaseFactory,
                TravelPurchaseFactory,
                TravelPurchaseService) {
    var vm = this;

    vm.ui = {};

    vm.proceed = function (planType) {
      TravelPurchaseService.policy.planType = planType;
      $state.go('travel-purchase-3');
    };

    function loadPlans() {
      vm.ui.planTypes = TravelPurchaseFactory.planTypes;

      PurchaseFactory.token().then( function(token) {
        angular.forEach(vm.ui.planTypes, function (planType) {
          PurchaseFactory.get(TRAVEL_TYPE, TRAVEL_FOLDER, generatePayload(planType), token).then(function (data) {
            planType.price = data.data.data.Outputs.BasePremium;
          });
        });
      });
    }

    function generatePayload(planType) {
      var payload = angular.copy(TRAVEL_PLANS_PAYLOAD);

      var DATE_FORMAT = 'YYYYDDD';

      payload.Inputs.Region = TravelPurchaseService.policy.whereAreYouGoing;
      payload.Inputs.Plan_Level = planType.name;
      payload.Inputs.Adults = TravelPurchaseService.policy.totalAdults;
      payload.Inputs.Children = TravelPurchaseService.policy.additionalChildren;
      payload.Inputs.Insured_Person_Self = TravelPurchaseService.policy.myself ? 'Y' : 'N';
      payload.Inputs.Insured_Person_Spouse = TravelPurchaseService.policy.spouse ? 'Y' : 'N';
      payload.Inputs.Plan = TravelPurchaseService.policy.tripCount;
      payload.Inputs.Travel_Start_Date = parseInt(moment(TravelPurchaseService.policy.departureDate).format(DATE_FORMAT), 10);
      payload.Inputs.Travel_End_Date = parseInt(moment(TravelPurchaseService.policy.arrivalDate).format(DATE_FORMAT), 10);
      payload.TransactionDate = moment().format('YYYY-MM-DDThh:mm:ss.ssssss') + 'Z';

      payload.Inputs.Optional_Benefits = angular.toJson(_.map(TravelPurchaseService.policy.options.benefits, function (option) {
        return {
          Option: option.Option,
          Included: 'n',
          ClaimLimit: 0
        }
      }));

      return payload;
    }

    function init() {
      TravelPurchaseService.debug();

      if(!TravelPurchaseService.policy.tripCount) {
        $log.debug('trip count is not defined. redirecting to step 1');
        $state.go('travel-purchase-1');
        return;
      }

      vm.ui.whereAreYouGoing = TravelPurchaseService.policy.whereAreYouGoing;
      vm.ui.departureDate = moment(TravelPurchaseService.policy.departureDate).format(DATE_FORMAT);
      vm.ui.arrivalDate = moment(TravelPurchaseService.policy.arrivalDate).format(DATE_FORMAT);
      vm.ui.totalAdults = TravelPurchaseService.policy.totalAdults;
      vm.ui.additionalChildren = TravelPurchaseService.policy.additionalChildren;
      vm.ui.planType = TravelPurchaseService.policy.planType;

      loadPlans();
    }

    init();
  }
})();

(function() {
  'use strict';

  angular
    .module('coherentPaDemo')
    .controller('TravelPurchase1Controller', ctrl);

  /** @ngInject */
  function ctrl($state,
                CALENDAR_FORMAT,
                DATE_FORMAT,
                OPTIONS_PAYLOAD,
                TRAVEL_TYPE,
                TRAVEL_FOLDER,
                PurchaseFactory,
                TravelPurchaseFactory,
                TravelPurchaseService) {
    var vm = this;

    vm.ui = {};

    vm.opts = {
      calendarFormat: CALENDAR_FORMAT,
      datepickerOptionsForDepartureDate: {
        showWeeks: false,
        minDate: moment(),
        maxDate: moment().add(275, 'days')
      },
      datepickerOptionsForArrivalDate: {
        showWeeks: false,
        minDate: moment(),
        maxDate: moment().add(275, 'days')
      },
      maxDependents: 10
    };

    vm.updateDatePickerOptions = function () {
      if (vm.ui.departureDate) {
        vm.opts.datepickerOptionsForArrivalDate.minDate = vm.ui.departureDate;
      }
    };

    vm.subtractAdults = function () {
      if (vm.ui.additionalAdults > 0) {
        vm.ui.additionalAdults -= 1;
      }
    };

    vm.addAdults = function () {
      if (vm.ui.additionalAdults + vm.ui.additionalChildren < vm.opts.maxDependents) {
        vm.ui.additionalAdults += 1;
      }
    };

    vm.subtractChildren = function () {
      if (vm.ui.additionalChildren > 0) {
        vm.ui.additionalChildren -= 1;
      }
    };

    vm.addChildren = function () {
      if (vm.ui.additionalAdults + vm.ui.additionalChildren < vm.opts.maxDependents) {
        vm.ui.additionalChildren += 1;
      }
    };

    vm.proceed = function () {
      TravelPurchaseService.policy.tripCount = vm.ui.tripCount;
      TravelPurchaseService.policy.whereAreYouGoing = vm.ui.whereAreYouGoing;
      TravelPurchaseService.policy.departureDate = moment(vm.ui.departureDate).format(DATE_FORMAT);
      TravelPurchaseService.policy.arrivalDate = moment(vm.ui.arrivalDate).format(DATE_FORMAT);
      TravelPurchaseService.policy.myself = vm.ui.myself;
      TravelPurchaseService.policy.spouse = vm.ui.spouse;
      TravelPurchaseService.policy.additionalAdults = vm.ui.additionalAdults;
      TravelPurchaseService.policy.totalAdults = vm.ui.myself + vm.ui.spouse + vm.ui.additionalAdults;
      TravelPurchaseService.policy.additionalChildren = vm.ui.additionalChildren;
      $state.go('travel-purchase-2');
    };

    function loadOptions() {
      PurchaseFactory.token().then( function(token) {
        PurchaseFactory.get(TRAVEL_TYPE, TRAVEL_FOLDER, generatePayload(), token).then(function (data) {
          var outputs = data.data.data.Outputs;

          TravelPurchaseService.policy.options.benefits = outputs['Options.BenefitOptions'];
          TravelPurchaseService.policy.options.coverageAreas = outputs['Options.CoverageAreaOptions'];
          TravelPurchaseService.policy.options.planLevels = outputs['Options.PlanLevelOptions'];

          vm.opts.countries = _.map(TravelPurchaseService.policy.options.coverageAreas, function (opt) {
            return {
              name: opt.Desc,
              value: opt.Option
            };
          });
        });
      });
    }

    function generatePayload() {
      var payload = angular.copy(OPTIONS_PAYLOAD);

      payload.TransactionDate = moment().format('YYYY-MM-DDThh:mm:ss.ssssss') + 'Z';
      return payload;
    }

    function init() {
      vm.ui.tripCount = TravelPurchaseService.policy.tripCount;
      vm.ui.whereAreYouGoing = TravelPurchaseService.policy.whereAreYouGoing;
      vm.ui.departureDate = TravelPurchaseService.policy.departureDate && moment(TravelPurchaseService.policy.departureDate).toDate();
      vm.ui.arrivalDate = TravelPurchaseService.policy.arrivalDate && moment(TravelPurchaseService.policy.arrivalDate).toDate();
      vm.ui.myself = TravelPurchaseService.policy.myself;
      vm.ui.spouse = TravelPurchaseService.policy.spouse;
      vm.ui.additionalAdults = TravelPurchaseService.policy.additionalAdults;
      vm.ui.additionalChildren = TravelPurchaseService.policy.additionalChildren;

      loadOptions();
    }

    init();
  }
})();

