(function() {
  'use strict';

  angular
    .module('coherentPaDemo')
    .controller('Purchase6Controller', ctrl);

  /** @ngInject */
  function ctrl($log,
                $state,
                ACCIDENT_TYPE,
                HOME_TYPE,
                DATE_FORMAT,
                PURCHASE_PAYLOAD,
                TRAVEL_TYPE,
                PurchaseFactory,
                PurchaseService,
                PolicyService) {
    var vm = this;

    vm.ui = {};

    vm.proceed = function() {
      PurchaseFactory.purchase(generatePayload()).then(function (data) {
        var policyNumber = data.data.PolicyID;
        PurchaseService.policy.number = policyNumber;
        PolicyService.policies = [{
          number: policyNumber
        }];
        $state.go('purchase-7');
      });
    };

    function generatePayload() {
      var payload = angular.copy(PURCHASE_PAYLOAD[PurchaseService.policy.type]);
      var policy = PurchaseService.policy;

      payload.Header.Agent = policy.agentCode;
      payload.Header.Email = policy.email;
      payload.Header.InstallmentPremium = policy.specs.installmentPermium;
      payload.Header.Mandate.CreditCard.CardNumber = vm.ui.cardNumber;
      payload.Header.Mandate.CreditCard.ExpiryMonth = vm.ui.expiryMM;
      payload.Header.Mandate.CreditCard.ExpityYear = vm.ui.expiryYY;
      payload.Header.Owner.Contact.Add.Add1 = policy.address;
      payload.Header.Owner.Contact.Add.Pcode = policy.postalCode1 + '-' + policy.postalCode2;
      payload.Header.Owner.Contact.Email = policy.email;
      payload.Header.Owner.Contact.FbId = policy.fbId;
      payload.Header.Owner.Contact.Phone = policy.contact;
      payload.Header.Owner.Dob = moment(policy.birthDate).format(DATE_FORMAT);
      payload.Header.Owner.Gender = policy.gender;
      payload.Header.Owner.Name = policy.name;
      payload.Header.StartDate = moment(policy.insuranceDate).format('YYYY-MM-DDThh:mm:ss.ssssss') + 'Z';
      payload.Risks[0].CoverType = policy.carrier + ':' + policy.type;
      payload.Risks[0].RiskBody.InsuredParty.Dob = moment(policy.birthDate).format(DATE_FORMAT);
      payload.Risks[0].RiskBody.InsuredParty.Gender = policy.gender;
      payload.Risks[0].RiskBody.InsuredParty.Name = policy.name;
      payload.Risks[0].RiskBody.Plan = policy.specs.planType
      payload.Risks[0].RiskBody.Planlevel = policy.specs.planLevel;
      payload.Risks[0].RiskBody.Risk_Comm = moment(policy.insuranceDate).format('YYYY-MM-DDThh:mm:ss.ssssss') + 'Z';
      payload.Risks[0].Risk_Comm = moment(policy.insuranceDate).format('YYYY-MM-DDThh:mm:ss.ssssss') + 'Z';
      if (PurchaseService.policy.type === TRAVEL_TYPE) {
        payload.Risks[0].RiskBody.Options = policy.specs.optionalBenefits ? _.map(policy.specs.optionalBenefits, function(benefit) {
          return {
            Option: benefit.name,
            Descr: benefit.label,
            Included: benefit.enabled ? 'Y' : 'N',
            SumInsured: benefit.enabled ? parseFloat(benefit.value) : 0
          };
        }) : [];
        payload.Risks[0].RiskBody.Self = policy.specs.myself ? 'Y' : 'N';
        payload.Risks[0].Self = policy.specs.myself ? 'Y' : 'N';
        payload.Risks[0].RiskBody.Spouse = policy.specs.spouse ? 'Y' : 'N';
        payload.Risks[0].Spouse = policy.specs.spouse ? 'Y' : 'N';
        payload.Risks[0].RiskBody.Nadults = policy.specs.totalAdults;
        payload.Risks[0].Nadults = policy.specs.totalAdults;
        payload.Risks[0].RiskBody.Nchildren = policy.specs.additionalChildren;
        payload.Risks[0].Nchildren = policy.specs.additionalChildren;
        payload.Risks[0].RiskBody.Risk_End = moment(policy.specs.arrivalDate).format('YYYY-MM-DDThh:mm:ss.ssssss') + 'Z';
        payload.Risks[0].Risk_End = moment(policy.specs.arrivalDate).format('YYYY-MM-DDThh:mm:ss.ssssss') + 'Z';
      }
      if (PurchaseService.policy.type === HOME_TYPE) {
        // load the yes / no ansers to the payload

        console.log('the benefit set ')
        console.log(PurchaseService.policy.specs.benefitSelection)
        console.log('-------')
        console.log('the risk options set ')
        console.log(payload.Risks[0].RiskBody.Options)
        console.log('-------')
        for(var i=0;i<PurchaseService.policy.specs.benefitSelection.length;i++) {
          // find corresponding item in payload
          // make it Included = "Y"
          var a = payload.Risks[0].RiskBody.Options.find(function(o) { o.Option === PurchaseService.policy.specs.benefitSelection[i].Option_Name });
          console.log('a= ')
          console.log(a)
          console.log('-------')
          var b = payload.Risks[0].RiskBody.Options.indexOf(a);
          console.log('a benefit')
          console.log(payload.Risks[0].RiskBody.Options[b])
          console.log('-------')
          if (a) {
            payload.Risks[0].RiskBody.Options[b].Included = PurchaseService.policy.specs.benefitSelection[i].enabled ? 'Y' : 'N'
          };
        };
        console.log(payload.Risks[0].RiskBody.Options)
      };
      return payload;
    }

    function init() {
      PurchaseService.debug();

      if (!PurchaseService.policy.type) {
        $log.debug('policy type is not defined. redirecting to step 1');
        $state.go('products');
        return;
      } else if (PurchaseService.policy.type === ACCIDENT_TYPE && !PurchaseService.policy.relationship) {
        $log.debug('relationship is not defined. redirecting to step 1');
        $state.go('products');
        return;
      } else if (PurchaseService.policy.type === TRAVEL_TYPE && !PurchaseService.policy.email) {
        $log.debug('email is not defined. redirecting to products');
        $state.go('products');
        return;
      }
    }

    init();

    return vm;
  }
})();

(function() {
  'use strict';

  angular
    .module('coherentPaDemo')
    .controller('Purchase5Controller', ctrl);

  /** @ngInject */
  function ctrl($log,
                $state,
                ACCIDENT_TYPE,
                TRAVEL_TYPE,
                HOME_TYPE,
                PurchaseFactory,
                PurchaseService) {
    var vm = this;

    vm.ui = {};

    vm.opts = {
      ACCIDENT_TYPE: ACCIDENT_TYPE,
      TRAVEL_TYPE: TRAVEL_TYPE
    };

    vm.proceed = function() {
      $state.go('purchase-6');
    };

    function init() {
      PurchaseService.debug();

      vm.ui.type = PurchaseService.policy.type;
      console.log(vm.ui.type === HOME_TYPE)

      if (!vm.ui.type) {
        $log.debug('type is not defined. redirecting to products');
        $state.go('products');
        return;
      }

      if (vm.ui.type === ACCIDENT_TYPE) {
        if (!PurchaseService.policy.relationship) {
          $log.debug('relationship is not defined. redirecting to products');
          $state.go('products');
          return;
        }
      }

      if (vm.ui.type === TRAVEL_TYPE) {
        if (!PurchaseService.policy.email) {
          $log.debug('email is not defined. redirecting to products');
          $state.go('products');
          return;
        }

        vm.ui.optionalBenefits = PurchaseService.policy.specs.optionalBenefits;
      }

      if (vm.ui.type === HOME_TYPE) {
        vm.ui.benefitSelection = PurchaseService.policy.specs.benefitSelection;
      }

      vm.ui.planType = PurchaseService.policy.specs.planType;
      vm.ui.plan = PurchaseService.policy.specs.plan;
      vm.ui.planDetails = PurchaseService.policy.specs.planDetails;

      vm.opts = _.merge(vm.opts, PurchaseFactory.opts[PurchaseService.policy.type]);
    }

    init();

    return vm;
  }
})();

