(function() {
  'use strict';

  angular
    .module('coherentPaDemo')
    .controller('Purchase2Controller', ctrl);

  /** @ngInject */
  function ctrl($log,
                $state,
                ACCIDENT_TYPE,
                TRAVEL_TYPE,
                HOME_TYPE,
                CALENDAR_FORMAT,
                PURCHASE_MIN_BIRTH_DATE,
                PURCHASE_MAX_BIRTH_DATE,
                PurchaseService) {
    var vm = this;

    vm.ui = {};

    vm.opts = {
      calendarFormat: CALENDAR_FORMAT,
      datepickerOptions: {
        showWeeks: false,
        minDate: PURCHASE_MIN_BIRTH_DATE,
        maxDate: PURCHASE_MAX_BIRTH_DATE
      }
    };

    vm.proceed = function () {
      if (!PurchaseService.signedIn) {
        PurchaseService.policy.email = vm.ui.email;
      }

      PurchaseService.policy.name = vm.ui.name;
      PurchaseService.policy.birthDate = vm.ui.birthDate;
      PurchaseService.policy.gender = vm.ui.gender;
      PurchaseService.policy.postalCode1 = vm.ui.postalCode1;
      PurchaseService.policy.postalCode2 = vm.ui.postalCode2;
      PurchaseService.policy.address = vm.ui.address;
      PurchaseService.policy.alternateEmail = vm.ui.alternateEmail;
      PurchaseService.policy.contact = vm.ui.contact;
      PurchaseService.policy.alternateContact = vm.ui.alternateContact;

      if(PurchaseService.policy.type === ACCIDENT_TYPE) {
        $state.go('purchase-3');
      } else if(PurchaseService.policy.type === TRAVEL_TYPE) {
        $state.go('purchase-5');
      } else if(PurchaseService.policy.type === HOME_TYPE) {
        $state.go('purchase-5');
      }
    };

    function init() {
      PurchaseService.debug();

      if(!PurchaseService.policy.specs.planType) {
        $log.debug('plan type is not defined. redirecting to products');
        $state.go('products');
        return;
      }

      vm.ui.signedIn = PurchaseService.signedIn;

      vm.ui.name = PurchaseService.policy.name;
      vm.ui.birthDate = PurchaseService.policy.birthDate && moment(PurchaseService.policy.birthDate).toDate();
      vm.ui.gender = PurchaseService.policy.gender;
      vm.ui.postalCode1 = PurchaseService.policy.postalCode1;
      vm.ui.postalCode2 = PurchaseService.policy.postalCode2;
      vm.ui.address = PurchaseService.policy.address;
      vm.ui.email = PurchaseService.policy.email;
      vm.ui.contact = PurchaseService.policy.contact;
      vm.ui.gender = 'M';
    }

    init();

    return vm;
  }
})();

(function() {
  'use strict';

  angular
    .module('coherentPaDemo')
    .controller('Purchase1Controller', ctrl);

  /** @ngInject */
  function ctrl($log,
                $state,
                PurchaseFactory,
                PurchaseService) {
    var vm = this;

    vm.ui = {};

    vm.ui.email = 'mark@email.com'

    vm.signIn = function () {
      PurchaseFactory.signIn(500).then(function (data) {
        PurchaseService.signedIn = true;

        PurchaseService.policy.email = vm.ui.email;

        PurchaseService.policy.name = data.name;
        PurchaseService.policy.birthDate = data.birthDate;
        PurchaseService.policy.gender = data.gender;
        PurchaseService.policy.postalCode1 = data.postalCode1;
        PurchaseService.policy.postalCode2 = data.postalCode2;
        PurchaseService.policy.address = data.address;
        PurchaseService.policy.contact = data.contact;

        $state.go('purchase-2');
      });
    };

    vm.skip = function () {
      $state.go('purchase-2');
    };

    function init() {
      PurchaseService.debug();

      if(!PurchaseService.policy.specs.planType) {
        $log.debug('plan type is not defined. redirecting to step 1');
        $state.go('products');
        return;
      };

      if(!PurchaseService.policy.email) { 
        vm.ui.email = PurchaseService.policy.email; 
      };
      vm.ui.email = 'mark@email.com'
    }

    init();

    return vm;
  }
})();

