(function() {
  'use strict';

  angular
    .module('coherentPaDemo')
    .factory('PurchaseFactory', fctry);

  /** @ngInject */
  function fctry($http,
                 $q,
                 $timeout,
                 ACCIDENT_TYPE,
                 TRAVEL_TYPE,
                 configUrls,
                 environment) {
    var vm = this;

    var headers = {
      'SecretKey': '762f5692-5a8b-4f8e-a09e-a27a277c0c56',
      'x-tenant-name': 'coherent',
      authorization: ''
    };

    vm.opts = {};

    vm.opts[ACCIDENT_TYPE] = {
      title: 'purchase_personal_accident_insurance',
      confirmation: 'purchase_personal_accident_is_a_product'
    };

    vm.opts[TRAVEL_TYPE] = {
      title: 'purchase_travel_insurance',
      confirmation: 'purchase_travel_is_a_product'
    };

    vm.relationships = [{
      value: 'self',
      display: "purchase_same_as_owner"
    }];

    vm.token = function() {
      return $http({
        method: 'GET',
        url: configUrls[environment].keyCloakTokenUrl + '?apiKey=' + configUrls[environment].keyCloakApiKey + '&client=' + configUrls[environment].keyCloakPFClient,
        headers: {
          'Content-Type': 'application/x-www-form-urlencoded'
        }
      });
    };

    vm.get = function (type, folder, data, token) {
      headers.authorization = 'Bearer ' + token.data.access_token

      return $http({
        method: 'POST',
        url: configUrls[environment].PFUrl + folder + '/engines/Execute/' + type,
        headers: headers,
        data: data
      });
    };

    vm.signIn = function (delay) {
      return $q(function(resolve) {
        $timeout(function() {
          resolve({
            name: 'Mark Banaria',
            birthDate: moment('1986-02-23'),
            gender: 'male',
            postalCode1: '1234',
            postalCode2: '4321',
            address: '12 Dove Street',
            contact: '+1291261111'
          });
        }, delay);
      });
    };

    vm.purchase = function (data) {
      // TODO: Change obj
      return $http({
        method: 'POST',
        url: configUrls[environment].createPolicy,
        data: data,
        headers: {
          'authorization': 'Bearer ' + configUrls[environment].token
        }
      });
    };

    return vm;
  }
})();

