(function() {
  'use strict';

  angular
    .module('coherentPaDemo')
    .controller('Policy9Controller', ctrl);

  /** @ngInject */
  function ctrl($log,
                $state,
                $stateParams,
                $translate,
                PolicyFactory,
                PolicyService) {
    var vm = this;

    vm.ui = {};

    vm.opts = {};

    vm.back = function () {
      if (vm.opts.mode === 'editSuccess' || vm.opts.mode === 'cancelRenewalSuccess') {
        $state.go('policy-3', {
          policyNumber: PolicyService.policy.number
        });
      } else {
        $state.go('policy-2');
      }
    };

    function init() {
      if(!PolicyService.policy.name) {
        $log.debug('policy did not load well. redirecting to step 1');
        $state.go('policy-1');
      }

      if ($stateParams.policyNumber !== PolicyService.policy.number) {
        $log.debug('policy number was modified. redirecting to step 1');
        $state.go('policy-1');
      }

      vm.opts.mode = $stateParams.mode && $stateParams.mode.replace(/-./g, function (x) {
        return x.toUpperCase()[1];
      });

      if (!PolicyFactory.confirmationMessages[vm.opts.mode]) {
        $log.debug('invalid mode. redirecting to step 1');
        $state.go('policy-1');
      }

      var confirmationMessage = PolicyFactory.confirmationMessages[vm.opts.mode];

      $translate(confirmationMessage).then(function(translated) {
        if (vm.opts.mode === 'cancelSuccess') {
          translated = translated.replace('RENEWAL_DATE', $stateParams.cancelDate);
        }
        vm.opts.confirmationMessage = translated;
      });

      if (vm.opts.mode === 'claimSuccess') {
        vm.ui.reference = PolicyService.policy.reference;
        vm.ui.payables = PolicyService.policy.grossPayables;
      }
    }

    init();

    return vm;
  }
})();

(function() {
  'use strict';

  angular
    .module('coherentPaDemo')
    .controller('Policy8Controller', ctrl);

  /** @ngInject */
  function ctrl($log,
                $state,
                $stateParams,
                CLAIM_PAYLOAD,
                DATE_FORMAT,
                PolicyFactory,
                PolicyService) {
    var vm = this;

    vm.ui = {};

    vm.opts = {
      withdrawalMethods: PolicyFactory.withdrawalMethods
    };

    vm.proceed = function() {
      PolicyFactory.claim(generatePayload()).then(function (data) {
        PolicyService.policy.reference = data.data.ClaimRef;
        $state.go('policy-9', {
          policyNumber: PolicyService.policy.number,
          mode: 'claim-success'
        });
      });
    }

    function generatePayload() {
      var payload = angular.copy(CLAIM_PAYLOAD);
      payload.Header.Payee.Bank = vm.ui.bankName;
      payload.Header.Payee.Branch = vm.ui.branchName;
      payload.Header.Payee.AccountName = vm.ui.accountName;
      payload.Header.Payee.Account = vm.ui.accountNumber;

      payload.Header.ClaimDate = moment().format('YYYY-MM-DDThh:mm:ss.ssssss') + 'Z';
      payload.Header.Contid = PolicyService.policy.number;
      payload.Header.Insured.Dob = moment(PolicyService.policy.insuredBirthDate).format(DATE_FORMAT);
      payload.Header.Insured.Gender = PolicyService.policy.insuredGender;
      payload.Header.Insured.Name = PolicyService.policy.insuredName;
      payload.Header.TransDate = moment().format('YYYY-MM-DDThh:mm:ss.ssssss') + 'Z';

      payload.Header.Notes = [];
      angular.forEach(PolicyService.policy.warningMessages, function (message) {
        var errorCode = message['ワーニングNo.']
        var text = errorCode + ' ' + PolicyFactory.errorCodes[errorCode];
        payload.Header.Notes.push({
          Dte: '2021-05-14T00:00:00Z',
          Text: text,
          User: 'AutoProcessing'
        });
      });

      return payload;
    }

    function init() {
      PolicyService.debug();

      if(!PolicyService.policy.name) {
        $log.debug('policy did not load well. redirecting to step 1');
        $state.go('policy-1');
      }

      if ($stateParams.policyNumber !== PolicyService.policy.number) {
        $log.debug('policy number was modified. redirecting to step 1');
        $state.go('policy-1');
      }

      vm.ui.bankName = PolicyService.policy.bankName;
      vm.ui.branchName = PolicyService.policy.branchName;
      vm.ui.accountName = PolicyService.policy.accountName;
      vm.ui.accountNumber = PolicyService.policy.accountNumber;
    }

    init();
  }
})();

(function() {
  'use strict';

  angular
    .module('coherentPaDemo')
    .controller('Policy7Controller', ctrl);

  /** @ngInject */
  function ctrl($log,
                $state,
                $stateParams,
                PolicyService,
                Upload) {
    var vm = this;

    vm.ui = {
      files: []
    };

    vm.uploadFiles = function (files) {
      if (files && files.length) {
        for (var i = 0; i < files.length; i++) {
          var file = files[i];

          Upload.upload({
            url: 'https://general.sonic.dev.coherent.com.hk/general/v2/s3/fileupload',
            data: {
              myFile: file
            }
          }).then(function (data) {
            var config = data.config;
            vm.ui.files.push({
              name: config.data.myFile.name,
              url: data.data,
              description: ''
            });
          });
        }
      }
    }

    vm.proceed = function() {
      PolicyService.policy.files = vm.ui.files;

      $state.go('policy-8', {
        policyNumber: PolicyService.policy.number
      });
    };

    function init() {
      PolicyService.debug();

      if(!PolicyService.policy.name) {
        $log.debug('policy did not load well. redirecting to step 1');
        $state.go('policy-1');
      }

      if ($stateParams.policyNumber !== PolicyService.policy.number) {
        $log.debug('policy number was modified. redirecting to step 1');
        $state.go('policy-1');
      }

      vm.ui.files = PolicyService.policy.files = [];
    }

    init();
  }
})();

(function() {
  'use strict';

  angular
    .module('coherentPaDemo')
    .controller('Policy6Controller', ctrl);

  /** @ngInject */
  function ctrl($log,
                $state,
                $stateParams,
                CALENDAR_FORMAT,
                CLAIM_VALIDATE_PAYLOAD,
                DATE_FORMAT,
                PolicyFactory,
                PolicyService) {
    var vm = this;

    vm.ui = {
      isOpen: false
    };

    var minDate = moment().subtract(1, 'month');
    var maxDate = moment();

    vm.opts = {
      calendarFormat: CALENDAR_FORMAT,
      datepickerOptions: {
        showWeeks: false,
        minDate: minDate,
        maxDate: maxDate
      },
      datepickerOptionsForHospitalStart: {
        showWeeks: false,
        minDate: minDate,
        maxDate: maxDate
      },
      datepickerOptionsForHospitalEnd: {
        showWeeks: false,
        minDate: minDate,
        maxDate: maxDate
      },
      datepickerOptionsForSurgery: {
        showWeeks: false,
        minDate: minDate,
        maxDate: maxDate
      },
      hospitalStatuses: PolicyFactory.hospitalStatuses
    };

    vm.updateDatePickerOptions = function () {
      if (vm.ui.incidentDate) {
        vm.opts.datepickerOptionsForHospitalStart.minDate = vm.ui.incidentDate;
      }

      if (vm.ui.hospitalStartDate) {
        vm.opts.datepickerOptionsForHospitalEnd.minDate = vm.ui.hospitalStartDate;
        vm.opts.datepickerOptionsForSurgery.minDate = vm.ui.hospitalStartDate;
      }

      if (vm.ui.hospitalEndDate) {
        vm.opts.datepickerOptionsForSurgery.maxDate = vm.ui.hospitalEndDate;
      }
    };

    vm.proceed = function () {
      PolicyService.policy.incidentDate = vm.ui.incidentDate && moment(vm.ui.incidentDate).format(DATE_FORMAT);
      PolicyService.policy.hospitalStatus = vm.ui.hospitalStatus;
      PolicyService.policy.hospitalStartDate = vm.ui.hospitalStartDate && moment(vm.ui.hospitalStartDate).format(DATE_FORMAT);
      PolicyService.policy.hospitalEndDate = vm.ui.hospitalEndDate && moment(vm.ui.hospitalEndDate).format(DATE_FORMAT);
      PolicyService.policy.surgeryDate = vm.ui.surgeryDate && moment(vm.ui.surgeryDate).format(DATE_FORMAT);
      PolicyService.policy.claimAmount = vm.ui.amount;
      PolicyService.policy.claimAddress = vm.ui.address;

      PolicyFactory.validate(PolicyService.policy.type, generatePayload()).then(function (data) {
        var outputs = data.data.data.Outputs;

        PolicyService.policy.warningMessages = outputs['Claim.Warning_Message'];
        PolicyService.policy.grossPayables = outputs['Claim.Gross_Payable'];

        $state.go('policy-7', {
          policyNumber: PolicyService.policy.number
        });
      });
    }

    function generatePayload() {
      var payload = angular.copy(CLAIM_VALIDATE_PAYLOAD);

      var DATE_FORMAT = 'YYYY-MM-DD hh:mm';

      payload.Inputs['Claim.Cover_StartDate'] = moment(PolicyService.policy.contractDate).format(DATE_FORMAT);
      payload.Inputs['Claim.Cover_Start_Date'] = moment(PolicyService.policy.contractDate).format(DATE_FORMAT);
      payload.Inputs['Claim.Cover_EndDate'] = moment(PolicyService.policy.renewalDate).format(DATE_FORMAT);
      payload.Inputs['Claim.Insured_Name'] = PolicyService.policy.insuredName;

      payload.Inputs['Claim.Policy_Limit'][0].Limit = PolicyService.policy.planType.components[0].PolicyLimit;
      payload.Inputs['Claim.Policy_Limit'][1].Limit = PolicyService.policy.planType.components[1].PolicyLimit;
      payload.Inputs['Claim.Policy_Limit'][2].Limit = PolicyService.policy.planType.components[2].PolicyLimit;
      payload.Inputs['Claim.Policy_Limit'][2].Limit_Daily = PolicyService.policy.planType.components[2].price;
      payload.Inputs['Claim.Policy_Limit'][3].Limit =  PolicyService.policy.planType.components[3].subComponents[0].PolicyLimit;
      payload.Inputs['Claim.Policy_Limit'][4].Limit =  PolicyService.policy.planType.components[3].subComponents[1].PolicyLimit;
      payload.Inputs['Claim.Policy_Limit'][5].Limit = PolicyService.policy.planType.components[4].PolicyLimit;

      payload.Inputs['Claim.AccDate'] = moment(vm.ui.incidentDate).format(DATE_FORMAT);
      payload.Inputs['Claim.PH_Name'] = PolicyService.policy.name;
      payload.Inputs['Claim.Claimant_Name'] = PolicyService.policy.insuredName;
      payload.Inputs['Claim.Hospital_EntryDate'] = vm.ui.hospitalStartDate && moment(vm.ui.hospitalStartDate).format(DATE_FORMAT);
      payload.Inputs['Claim.Hospital_ExitDate'] = vm.ui.hospitalEndDate && moment(vm.ui.hospitalEndDate).format(DATE_FORMAT);
      payload.Inputs['Claim.Hospital_Status'] = vm.ui.hospitalStatus;
      payload.Inputs['Claim.Surgery_Date'] = vm.ui.surgeryDate && moment(vm.ui.surgeryDate).format(DATE_FORMAT);
      payload.Inputs['Claim.TP_Amt'] = vm.ui.amount;

      return payload;
    }

    function init() {
      PolicyService.debug();

      if(!PolicyService.policy.name) {
        $log.debug('policy did not load well. redirecting to step 1');
        $state.go('policy-1');
      }

      if ($stateParams.policyNumber !== PolicyService.policy.number) {
        $log.debug('policy number was modified. redirecting to step 1');
        $state.go('policy-1');
      }

      _.merge(vm.opts, PolicyFactory.opts[PolicyService.policy.type]);

      vm.ui.name = PolicyService.policy.name;
      vm.ui.insuredName = PolicyService.policy.insuredName;

      vm.ui.incidentDate = PolicyService.policy.incidentDate && moment(PolicyService.policy.incidentDate).toDate();
      vm.ui.hospitalStatus = PolicyService.policy.hospitalStatus;
      vm.ui.hospitalStartDate = PolicyService.policy.hospitalStartDate && moment(PolicyService.policy.hospitalStartDate).toDate();
      vm.ui.hospitalEndDate = PolicyService.policy.hospitalEndDate && moment(PolicyService.policy.hospitalEndDate).toDate();
      vm.ui.surgeryDate = PolicyService.policy.surgeryDate && moment(PolicyService.policy.surgeryDate).toDate();
      vm.ui.amount = PolicyService.policy.claimAmount;
      vm.ui.address = PolicyService.policy.claimAddress;
    }

    init();
  }
})();

(function() {
  'use strict';

  angular
    .module('coherentPaDemo')
    .controller('Policy5Controller', ctrl);

  /** @ngInject */
  function ctrl($log,
                $state,
                $stateParams,
                CALENDAR_FORMAT,
                DATE_FORMAT,
                CANCEL_PAYLOAD,
                REFUND_PAYLOAD,
                PolicyFactory,
                PolicyService) {
    var vm = this;

    vm.ui = {};

    vm.opts = {
      calendarFormat: CALENDAR_FORMAT,
      datepickerOptions: {
        showWeeks: false
      },
      reasons: PolicyFactory.cancellationReasons,
      withdrawalMethods: PolicyFactory.withdrawalMethods
    };

    vm.getPrice = function () {
      $log.debug('get price');

      return PolicyFactory.getRefundPrice(PolicyService.policy.type, generateRefundPayload()).then(function (data) {
          vm.ui.refundPremium = data.data.data.Outputs.RefundPremium;
      });
    };

    function generateRefundPayload() {
      var payload = angular.copy(REFUND_PAYLOAD);

      var DATE_FORMAT = 'YYYY-MM-DD hh:mm:ss';

      payload.Inputs.CancellationDate = moment(vm.ui.cancelDate).format(DATE_FORMAT);
      payload.Inputs.Start_Date = payload.Inputs.BillPeriodStart = moment(PolicyService.policy.contractDate).format(DATE_FORMAT);
      payload.Inputs.BillPeriodEnd = moment(PolicyService.policy.contractDate).add(1 , 'year').format(DATE_FORMAT);
      payload.Inputs.CancelAllocatedPremium = PolicyService.policy.allocated;
      payload.Inputs.CancelRef = vm.ui.reason;

      return payload;
    }

    vm.proceed = function () {
      PolicyFactory.cancel(generateCancelPayload()).then(function () {
        $state.go('policy-9', {
          policyNumber: PolicyService.policy.number,
          mode: 'cancel-success',
          cancelDate: moment(vm.ui.cancelDate).format(DATE_FORMAT)
        });
      });
    };

    function generateCancelPayload() {
      var payload = angular.copy(CANCEL_PAYLOAD);

      payload.PolicyID = PolicyService.policy.number;
      payload.TranDate = moment().format('YYYY-MM-DDThh:mm:ss.ssssss') + 'Z',
      payload.CancelDate = moment(vm.ui.cancelDate).format(DATE_FORMAT);
      payload.CancelReason = vm.ui.reason;

      return payload;
    }

    function init() {
      PolicyService.debug();

      if(!PolicyService.policy.name) {
        $log.debug('policy did not load well. redirecting to step 1');
        $state.go('policy-1');
      }

      if ($stateParams.policyNumber !== PolicyService.policy.number) {
        $log.debug('policy number was modified. redirecting to step 1');
        $state.go('policy-1');
      }

      vm.ui.price = 0;
      vm.ui.reason = vm.opts.reasons[0];
    }

    init();

    return vm;
  }
})();

