(function() {
  'use strict';

  angular
    .module('coherentPaDemo')
    .controller('HomePurchase2Controller', ctrl);

  /** @ngInject */
  function ctrl($state,
                CALENDAR_FORMAT,
                DATE_FORMAT,
                HOME_TYPE,
                HOME_FOLDER,
                PurchaseFactory,
                HomePurchaseService,
                PurchaseService,
                HOME_SPARK_PAYLOAD,
                HOME_PLANS_PAYLOAD) {
    var vm = this;

    vm.ui = {};
    vm.ui.benefitSelection = HomePurchaseService.policy.benefitSelection

    vm.proceed = function () {
      PurchaseService.policy.type = HOME_TYPE;
      PurchaseService.policy.carrier = HOME_FOLDER;
      PurchaseService.policy.specs = HomePurchaseService.policy;
      PurchaseService.policy.specs.planDetails = {
        title: PurchaseService.policy.specs.plan.title,
        price: vm.ui.price
      }
      PurchaseService.policy.specs.installmentPermium = vm.ui.price
      console.log(PurchaseService.policy.specs)
      $state.go('purchase-1');
    };


    function generateInputPayload() {
      var payload = angular.copy(HOME_SPARK_PAYLOAD);

      payload.TransactionDate = moment().format('YYYY-MM-DDThh:mm:ss.ssssss') + 'Z';
      return payload;
    }

    function generatePayload() {
      var payload = angular.copy(HOME_PLANS_PAYLOAD);

      payload.TransactionDate = moment().format('YYYY-MM-DDThh:mm:ss.ssssss') + 'Z';
      for(var i=0;i<HomePurchaseService.policy.benefitSelection.length;i++) {
        // find corresponding item in payload
        // make it Included = "Y"
        var a = payload.Inputs.Optional_Benefits.find(function(o) { o.Option === HomePurchaseService.policy.benefitSelection[i].Option_Name } );
        var b = payload.Inputs.Optional_Benefits.indexOf(a)
        if (a) {
          payload.Inputs.Optional_Benefits[b].Included = HomePurchaseService.policy.benefitSelection[i].enabled ? 'Y' : 'N'
        }
      }
      console.log(payload.Inputs.Optional_Benefits)
      return payload;
    }

    function loadOptions() {
      PurchaseFactory.token().then( function(token) {
        PurchaseFactory.get(HOME_TYPE, HOME_FOLDER, generateInputPayload(), token).then(function (data) {
          var outputs = data.data.data.Outputs;
          HomePurchaseService.policy.benefitSelection = outputs['Options.Benefit_Selection'];
          for(var i=0;i<HomePurchaseService.policy.benefitSelection.length;i++) {
            HomePurchaseService.policy.benefitSelection[i].enabled = true
          }
          vm.ui.benefitSelection = HomePurchaseService.policy.benefitSelection
          console.log(vm.ui.benefitSelection)
          vm.loadPrice();
        });
      });
    }    

    vm.loadPrice = function() {
      PurchaseFactory.token().then( function(token) {
        PurchaseFactory.get(HOME_TYPE, HOME_FOLDER, generatePayload(), token).then(function (data) {
          var outputs2 = data.data.data.Outputs;
          HomePurchaseService.policy.price = outputs2.InstallmentPremium;
          vm.ui.price = HomePurchaseService.policy.price
        });
      });
    }

    function init() {

      loadOptions();
    }

    init();
  }
})();

(function() {
  'use strict';

  angular
    .module('coherentPaDemo')
    .controller('HomePurchase1Controller', ctrl);

  /** @ngInject */
  function ctrl($state,
                CALENDAR_FORMAT,
                DATE_FORMAT,
                PurchaseFactory,
                HomePurchaseService) {
    var vm = this;

    vm.ui = {};
    vm.ui.propertyAddress = {
      unitNumber: '',
      streetAddress: '',
      postCode: ''
    }

    vm.opts = {
      calendarFormat: CALENDAR_FORMAT,
      datepickerOptionsForInsuranceDate: {
        showWeeks: false,
        minDate: moment(),
        maxDate: moment().add(275, 'days')
      }
    }

    vm.proceed = function () {
      HomePurchaseService.policy.insuranceDate = moment(vm.ui.insuranceDate).format(DATE_FORMAT)
      HomePurchaseService.policy.propertyOwnership = vm.ui.propertyOwnership
      HomePurchaseService.policy.propertyAddress.unitNumber = vm.ui.propertyAddress.unitNumber
      HomePurchaseService.policy.propertyAddress.streetAddress = vm.ui.propertyAddress.streetAddress
      HomePurchaseService.policy.propertyAddress.postCode = vm.ui.propertyAddress.postCode
      $state.go('home-purchase-2');
    };

    function init() {
      vm.ui.propertyAddress.unitNumber = '12-02' ;
      vm.ui.propertyAddress.streetAddress = '12 Dove Street';
      vm.ui.propertyAddress.postCode = '2332-2331';
    }

    init();
  }
})();

