(function() {
  'use strict';

  angular
    .module('coherentPaDemo')
    .controller('AccidentPurchase4Controller', ctrl);

  /** @ngInject */
  function ctrl($log,
                $state,
                ACCIDENT_TYPE,
                ACCIDENT_FOLDER,
                ACCIDENT_PLANS_PAYLOAD,
                AccidentPurchaseFactory,
                AccidentPurchaseService,
                PurchaseFactory,
                PurchaseService) {
    var vm = this;

    vm.ui = {};

    vm.select = function (planType) {
      vm.ui.planTypeId = planType.id;
      vm.ui.planType = planType;
    };

    vm.proceed = function () {
      PurchaseService.policy.type = ACCIDENT_TYPE;
      PurchaseService.policy.carrier = ACCIDENT_FOLDER;
      PurchaseService.policy.specs = AccidentPurchaseService.policy;
      PurchaseService.policy.specs.planType = vm.ui.planType.code
      PurchaseService.policy.specs.planDetails = vm.ui.planType
      console.log(PurchaseService.policy.specs.planDetails)
      PurchaseService.policy.specs.installmentPermium = vm.ui.planType.price
      //AccidentPurchaseService.policy.planType = vm.ui.planType;

      $state.go('purchase-1');
    };

    function loadPlans() {
      vm.ui.planType = AccidentPurchaseService.policy.planType;
      vm.ui.planTypes = _.map(AccidentPurchaseFactory.planTypes, function (planType) {
        planType.components = angular.copy(AccidentPurchaseFactory.planTypeComponents);
        return planType;
      });

      PurchaseFactory.token().then( function(token) {
        angular.forEach(vm.ui.planTypes, function (planType, idx) {
          PurchaseFactory.get(ACCIDENT_TYPE, ACCIDENT_FOLDER, generatePayload(planType.code), token).then(function (data) {
            var prices = data.data.data.Outputs;
            vm.ui.planTypes[idx].price = prices.BasePremium;
            vm.ui.planTypes[idx].components[0].price = prices.BenefitLimits[0].ClaimLimit;
            vm.ui.planTypes[idx].components[1].price = prices.BenefitLimits[1].ClaimLimit;
            vm.ui.planTypes[idx].components[2].price = prices.BenefitLimits[2].DailyLimit;
            vm.ui.planTypes[idx].components[3].subComponents[0].price = prices.BenefitLimits[3].ClaimLimit;
            vm.ui.planTypes[idx].components[3].subComponents[1].price = prices.BenefitLimits[4].ClaimLimit;
            vm.ui.planTypes[idx].components[4].price = prices.BenefitLimits[5].ClaimLimit;
          });
        });
      });
    }

    function generatePayload(code) {
      var payload = angular.copy(ACCIDENT_PLANS_PAYLOAD);
      payload.Inputs.Plan = code;
      payload.Inputs.Coverage = 'Insured_' + AccidentPurchaseService.policy.plan.coverage;
      return payload;
    }

    function init() {
      AccidentPurchaseService.debug();

      if(!AccidentPurchaseService.policy.plan) {
        $log.debug('plan is not defined. redirecting to step 1');
        $state.go('accident-purchase-1');
        return;
      }

      loadPlans();
    }

    init();

    return vm;
  }
})();

