(function() {
  'use strict';

  angular
    .module('coherentPaDemo')
    .controller('AccidentPurchase1Controller', ctrl);

  /** @ngInject */
  function ctrl($log,
                $state,
                ACCIDENT_TYPE,
                CALENDAR_FORMAT,
                DATE_FORMAT,
                AccidentPurchaseService) {
    var vm = this;

    vm.ui = {};

    vm.opts = {
      calendarFormat: CALENDAR_FORMAT,
      datepickerOptions: {
        showWeeks: false,
        minDate: moment(),
        maxDate: moment().add(30, 'd')
      }
    };

    vm.proceed = function() {
      AccidentPurchaseService.policy.insuranceDate = moment(vm.ui.insuranceDate).format(DATE_FORMAT);

      $state.go('accident-purchase-2');
    };

    function init() {
      AccidentPurchaseService.debug();

      vm.ui.checkbox = !!AccidentPurchaseService.policy.insuranceDate;
      vm.ui.insuranceDate = AccidentPurchaseService.policy.insuranceDate && moment(AccidentPurchaseService.policy.insuranceDate).toDate();
    }

    init();

    return vm;
  }
})();

